*{
  box-sizing: border-box;
}

::-webkit-scrollbar {
  height: 6px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
  width: 10px;
}
 
::-webkit-scrollbar-thumb {
  background: red; 
  border-radius: 5px;
  width: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #b30000; 
  height: 10px;
}

body {
  overflow: hidden;
  background: #212121;
}

*{
  color: #212121;
  text-transform: uppercase;
}

.file-upload{
  display: none;
}


.main{
  min-height: 100vh;
  display: grid;
  grid-template-rows: 99.99fr 0.01fr;
}

.container{
  display: grid;
  grid-template-rows: 10fr 3fr;
}

.panel{
  display: grid;
  grid-template-columns: 8.5fr 1.5fr;
}

.image-canvas{
  display: flex;
  align-items: center;
  justify-content: center;
}

.canvas{
  position: absolute;
  display: inline;
}

.draw{
  display: inline;
}

.draw .canv-upload{
  height: 150px;
  cursor: pointer;
}

.box{
  box-shadow: 0px 0px 0px 0px #212121;
  transition-property: all;
  transition-duration: 200ms;
  z-index: 3;
}

.box:hover{
  box-shadow: 0px 0px 10px 0.005em #212121;
  z-index: 9999;
}

.mask{
  position: absolute;
  top: 0.05em;
  opacity: 0.8;
  image-rendering:optimizeSpeed;
  transition-property: all;
  transition-duration: 300ms;
  z-index: -1;
}

.mask:hover{
  opacity: 0.95;
}

.upload{
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fafafa;
}

.upload-tut{
  
}



.upload svg{
  height: inherit;
}

.upload label{
  font-weight: 700;
  font-size: 1vh;
}

.label-drag{
  height: 30px;
  cursor: pointer;
}


.gallery{
  display: grid;
  grid-template-columns: 1fr 19fr;
  border: solid #fafafa 3px;
}

.images{
  display: flex;
  align-items: center;
  overflow-x: scroll;
  margin-left: 15px;
}

.images img{
  height: 17vh;
  margin-right: 20px;
  cursor: pointer;
  transition-property: all;
  transition-duration: 200ms;
}

.img-div{
  display: flex;
  justify-content: center;
  align-items: center;
}

.img-selected{
  margin: 0px 10px;
}

.images .selected{
  box-shadow: 0px 0px 3px 0.01em #212121;
  transform: scale(1.12);
}

.box-panel{
  display: grid;
  grid-template-rows: 8% 91%;
  animation-name: panelin;
  animation-duration: 700ms;
}

@keyframes panelin{
  0%{
    transform: translateY(-10vh);
  }
  75%{
    transform: translateY(5vh);
  }
}


.image-name{
  padding: 5px;
  margin: 10px 10px 5px 5px;
  font-size: 1.5vh;
  letter-spacing: 1px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: solid #212121e8 0.03vh;
  background: #fafafa;
  border-radius: 2px;
}

.box-rows{
  background: #fafafa;
  border-radius: 2px;
  padding: 5px 0px;
  margin: 5px 10px 5px 5px;
  display: grid;
  grid-template-rows: 1fr;
  height: fit-content;
  grid-gap: 5px;
}

.box-row{
  font-size: 1.3vh;
  letter-spacing: 1px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 20px 0px 15px;
  height: 2.5vh;
  cursor: inherit;
}

.box-row input{
  display: none;
  position: relative;
  z-index: -1;
}

.box-tut{

}

.box-tut::before{
  content: "To Select Class Click On The Checkboxes In Right Panel";
  position: absolute;
  left: 5px;
  top: 5px;
  width: 13vw;
  color: #fafafa;
  font-size :1.34vh;
  letter-spacing: 3px;
  font-weight: 600;
  border: solid 3px #fafafa;
  padding: 6px;
  line-height: 2vh;
  animation-name: notif-in;
  animation-duration: 700ms;
}

.upload-tut::before{
  content: "Click On Upload Button";
  position: absolute;
  top: 5px;
  left: 5px;
  color: #fafafa;
  letter-spacing: 3px;
  font-size: 1.35vh;
  font-weight: 600;
  border: solid 3px #fafafa;
  padding: 5px;
  animation-name: notif-in;
  animation-duration: 700ms;
}

@keyframes notif-in{
  0%{
    transform: translateX(-20vw);
  }
  75%{
    transform: translateX(2vw);
  }
  100%{
    transform: translateX(0);
  }
}

.checkbox {
  height: 2vh;
  width: 2vh;
  cursor: pointer;
  transition-property: all;
  transition-duration: 200ms;
  border-radius: 50%;
}


.loader{
  display: flex;
  justify-content: center;
  align-items: center;
}

.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #fff;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
  left: 8px;
  animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
  left: 8px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
  left: 32px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
  left: 56px;
  animation: lds-ellipsis3 0.6s infinite;
}
@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}
